<!-- eslint-disable vue/multi-word-component-names -->
<template>
  <article class="mb-8 mb-md-15">
    <!-- 僅作為標題結構，但不需顯示在畫面上 -->
    <h2 class="visually-hidden">活動集錦・ALBUMS</h2>

    <!-- 跑馬燈 -->
    <ScrollingText />

    <!-- Albums -->
    <div class="container my-12 my-md-15">
      <ul class="row justify-content-center justify-content-md-start row-gap-11">
        <li v-for="(album, index) in albums" :key="album.id" class="col-10 col-md-6 col-xl-3">
          <AlbumCard :album="album" :index="index" />
        </li>
      </ul>
    </div>

    <div class="container my-12 my-md-15">
      <h3 class="text-center mb-4">Swiper 範例</h3>
      <!-- <Swiper
        :modules="swiperModules"
        :navigation="true"
        :spaceBetween="24"
        :direction="albumSwiper.direction"
        :slidesPerView="albumSwiper.slidesPerView"
        :loop="albumSwiperLoop"
        :autoplay="{
          delay: 3000,
        }"
        :class="albumSwiper.class"
      > -->
      <Swiper
        :modules="swiperModules"
        :navigation="true"
        :spaceBetween="24"
        :direction="albumSwiper.direction"
        :slidesPerView="albumSwiper.slidesPerView"
        :loop="albumSwiperLoop"
        :class="albumSwiper.class"
      >
        <SwiperSlide
          v-for="(album, index) in albums"
          :key="album.id"
          class="col-10 col-md-6 col-xl-3"
        >
          <AlbumCard :album="album" :index="index" />
        </SwiperSlide>
      </Swiper>
    </div>

    <!-- 跑馬燈 -->
    <ScrollingText :isReverse="true" />
  </article>
</template>

<script>
// eslint-disable-next-line no-unused-vars
import { AlbumRecord } from "@/services/data/album";
import { useAlbums } from "@/composables/data/useAlbums";
import ScrollingText from "@/components/home/albums/ScrollingText.vue";
import AlbumCard from "@/components/home/albums/AlbumCard.vue";

/* Swiper 相關 */
// Import Swiper Vue.js components
import { Swiper, SwiperSlide } from "swiper/vue";
// Import required modules
import { Navigation, Pagination, Scrollbar, Autoplay } from "swiper/modules";
// Import Swiper styles
import "swiper/css";
import "swiper/css/navigation";
import "swiper/css/pagination";
import "swiper/css/scrollbar";

// TODO 做成輪播
export default {
  /**
   * @typedef {Object} AlbumsData
   * @property {AlbumRecord[]} albums 相簿資料。
   */
  /** @returns {AlbumsData} */
  data() {
    return {
      albums: [], // 相簿資料
      swiperModules: [Navigation, Pagination, Scrollbar, Autoplay], // Swiper 模組
      albumSwiper: {
        slidesPerView: 1, // 每次顯示的幻燈片數量
        // direction: "horizontal", // 幻燈片方向
        direction: "vertical", // 幻燈片方向
        class: ["vertical"],
        // class: [],
      },
    };
  },

  created() {
    // 取得相簿資料
    useAlbums().then(({ albums }) => {
      // TODO TEST
      console.log(`albums ==>`, albums.value);
      // this.albums = albums;
      this.albums = albums.value.slice(0, 5);

      // 初始化 Swiper 設定
      this.updateSwiperByWidth();
    });
  },

  mounted() {
    // 監聽螢幕寬度變化，動態調整 Swiper 設定
    window.addEventListener("resize", this.updateSwiperByWidth);
  },

  methods: {
    // TODO 手機版使用直式 4 個
    /**
     * 根據螢幕寬度設定 Swiper。
     */
    updateSwiperByWidth() {
      // 手機
      if (window.matchMedia("(max-width: 768px)").matches) {
        // this.albumSwiper.slidesPerView = 1;
        this.albumSwiper.slidesPerView = 4;
      }
      // 平板
      else if (window.matchMedia("(max-width: 1200px)").matches) {
        this.albumSwiper.slidesPerView = 2;
      }
      // 桌機
      else {
        this.albumSwiper.slidesPerView = 4;
      }
    },
  },
  // method

  computed: {
    /**
     * 相簿輪播的循環播放屬性。
     * @returns {boolean} 是否啟用循環播放。
     */
    albumSwiperLoop() {
      // 相簿數量 必須大於 每次顯示的幻燈片數量 才能啟用循環播放
      return this.albums.length > this.albumSwiper.slidesPerView;
    },
  },

  components: {
    ScrollingText,
    AlbumCard,
    Swiper,
    SwiperSlide,
  },
};
</script>

<style scoped lang="scss">
@use "@/assets/scss/mixins/rwd";

// 覆寫 Swiper 的樣式
.swiper {
  /*
   由於卡片因為旋轉而呈現不規則，超出的部份會因為 Swiper 特性而變成看不見。
   因此這邊需要額外設定 padding 將內容推擠至可視區。
  */
  padding-top: 25px;
  padding-bottom: 10px;
  padding-left: 10px;
  padding-right: 10px;

  // 手機版的卡片寬度非佔滿 100%，需要再另外限縮手機版的寬度。
  @include rwd.pad-down {
    width: 89%;
  }

  // 垂直方向的 Swiper
  &.vertical {
    height: 1566px;
  }
}

// 覆寫 Swiper 的 slide 樣式
.swiper-slide {
  font-size: 30px;
  font-weight: bold;
  display: flex;
  justify-content: center;
  align-items: center;
}
</style>
